import React,{Component} from 'react';
import store from '../store.js';


class Shop extends Component{
	constructor(){
		super()
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'listPage'
		})
	}
	
	render(){
		return(
			<div className="dianpu">
				<div className="shopActive">
					<h2></h2>
					<p>
						<span className="redFont pd"></span>
						<span>251045157820</span>
					</p>
					<p>
						<span className="green pd"></span>
						<span>17</span>
					</p>
					<p>
						<span  className="pd"></span>
						<span></span>
					</p>
				
				</div>
				<div className="shopImg">
					<h2></h2>
					<div><img src="https://qlogo2.store.qq.com/qzone/1542376969/1542376969/100?1507981281"/><img src="https://qlogo2.store.qq.com/qzone/1542376969/1542376969/100?1507981281"/></div>
				</div>
			
				<div className="shopAbout">
					<h2></h2>
					<p className="shopBet"><span></span><span>24536778{">"}</span></p>
					<p className="shopBet"><span></span><span></span></p>
					<p><span></span><span>10:30-22:00</span></p>				
				</div>
				<div className="shopBet"><h2></h2><span>{">"}</span></div>
		</div>
		)
	}
}


export default Shop;



