import React,{Component} from 'react';
import store from '../store.js';


class WodeZhanghao extends Component{
	constructor(){
		super()
		this.goPage=this.goPage.bind(this)
		
	}
	goPage(){
		console.log(store.getState());
		store.dispatch({
			type:'wode'
		})
	}
	
	render(){
		return (
			<div className="findPage">
				<div className="findTop">
					<span onClick={this.goPage}>{'<'}</span>
					
				</div>
				<div>
					<div></div>
					<div><img/><span>{">"}</span></div>
				</div>
				<div>
					<div></div>
					<div><span>3024993757</span><span>{">"}</span></div>
				</div>
				<p></p>
				<div>
					<div><span>$</span></div>
					<div><span>143658679780</span><span>{">"}</span></div>
				</div>
				<p></p>
				<div>
					<div></div>
					<div><span></span><span>{">"}</span></div>
				</div>
				<p></p>
				<div>
					
				</div>
</div>
			)
	}
}


export default WodeZhanghao;



